<template>
  <div id="inputList">
  	<input type="text" name="input" v-model="task.content" @keydown.enter="addItem()" class='edit' placeholder="输入一些要做的事情">
  		<ul v-if="inputList.length" class="task">
  			<li v-for="(item,index) in inputList">
  				<input type="checkbox" v-bind:checked="item.finish" @click="changeChecked(index)"><span v-bind:class="{'finish':item.finish}">{{index+1}}. {{item.content}}</span><button v-show="item.finish" class="finishButton">已完成</button><button class="delete" @click="deleteItem(index)">删除</button>
  			</li>
  		</ul>
  	<div v-else="inputList.length" class="task">抱歉，暂无任务</div>		
  </div>
</template>

<script>

export default {
  data () {
    return {
      task: {
        content: '',
        finish: false
      },
      inputList: []
    }
  },
  methods: {
    addItem () {
      this.inputList.push(this.task)
      this.task = {
        content: '',
        finish: false
      }
    },
    deleteItem (index) {
      this.inputList.splice(index, 1)
    },
    changeChecked (index) {
      var state = this.inputList[index].finish
      this.inputList[index].finish = !state
    }
  }
}
</script>

<style>
*{
	padding: 0;
	margin: 0;
}
 .edit{
    display:block ;
    width:80%;
    height: 35px;
    line-height: 35px;
    margin: 30px auto;
    box-sizing: border-box;
    padding-left: 4px;
    border-radius: 4px;
    border:1px solid #ccc;
    outline: 0;
    box-shadow: 0 0 5px #ccc;
 	text-indent: 10px;
 }
 .task{
 	display:block ;
    width:80%;
     margin: 30px auto;
 }
 ul,li{
 	list-style: none;
 }
 ul li{
 	padding: 10px 0;
 }
 .delete{
 	display: inline-block;
 	background-color: #f00000;
 	color: #fff;
 	padding: 2px;
 	border:none;
 	margin-left: 10px;
 	border-radius: 5px;
 }
 .finish{
 	text-decoration: line-through;
 	color: #ccc;	
 }
 .finishButton{
 	background-color: green;
 	color: #fff;
 	display: inline-block;
 	border:none;
 	margin-left: 10px;
 	border-radius: 5px;
 	padding: 2px;
 }
</style>



